import axios from '../interception'

const API = {
    /*post*/
    getUsers(data) {
        return axios({
            method: 'post',
            url: 'getusers',
            data: data,
        })
    },
    addUser(data) {
        return axios({
            method: 'post',
            url: 'adduser',
            data: data,
        })
    },
}

export default API;
