<template>
   <div>
        <input placeholder="用户名" v-model="username"/>
       <input placeholder="用户名" v-model="password"/>
       <button @click="add">提交</button>
   </div>
</template>

<script>
    import API from '../requests/apis'
    export default {
        name: "index",
        data(){
            return{
                username:'',
                password:''
            }
        },
        methods:{
            add(){
                API.addUser({
                    username:this.username,
                    password:this.password
                }).then(res=>{
                    console.log('------------------------',res)
                })
            },
            getUsers(){
                API.getUsers({}).then(res=>{
                    console.log('------------------------',res)
                })
            }
        },
        created(){
            this.getUsers()
        }
    }
</script>

